import React, { createContext, useContext, useEffect, useState } from "react";
import jwtDecode from 'jwt-decode';
import { Config } from '../../../config';
import api from "../../../utils/api";

// Create Authentication Context
const AuthContext = createContext(null);
export const useAuth = () => useContext(AuthContext);

// Auth Provider
export const ProvideAuth = ({ children }) => {
  const auth = useProvideAuth();
  return <AuthContext.Provider value={auth}>{children}</AuthContext.Provider>;
};

const useProvideAuth = () => {
  const [jwt, setJWT] = useState(null);
  const [user, setUser] = useState(null);
  const isAuthed = (user && jwt && jwtDecode(jwt).exp * 1000 > Date.now()) ? true : false;

  // Get current user data, if they are logged in
  useEffect(() => {
    if(sessionStorage.getItem('jwt')) {
      setJWT(sessionStorage.getItem('jwt'));
    } else if(localStorage.getItem('jwt')) {
      setJWT(localStorage.getItem('jwt'));
    }

    // If JWT is stored then load user from api
    if (jwt && jwt !== null) {
      api.getJWTUser(jwt)
      .then((response) => {
        setUser(response.data)
      })
      .catch(function (error) {
          console.log(error)
          setUser(null)
      })
    }
    // console.log(`User object: ${user}`);
    // console.log(`Is authed: ${isAuthed}`);

    return () => {
      setUser(null);
    };
  }, [jwt]);

  // Handle signing in
  const signin = async (username, password, remember) => {
    return api.authenticate({ 
      username: username, 
      password: password
    })
    .then(function (response) {
        if(Config.Logging) 
          console.log(`Sign in Response: ${response}`);
        // Hey Hacker! This is great for XSS isn't it?
        if (remember) { // Local storage, Remember the user after they close the browser
          localStorage.setItem('jwt', response.data?.token);
          setUser(response.data);
        } else { // Session storage, Don't remember the user after they close the browser
          sessionStorage.setItem('jwt', response.data?.token);
          setUser(response.data);
        }
        return response;
    })
    .catch(function (error) {
        console.log(`Sign in Error: ${error}`);
        return error;
    })
  };

  // Handle sign out
  const signout = () => {
    console.log('Sign Out Called:');
    console.log(user);
    console.log(jwt);

    // Call API
    return api.logout(jwt)
    .then(function () {
      localStorage.removeItem('jwt');
      sessionStorage.removeItem('jwt');
      setUser(null)
    })
    .catch(function(error) {
      console.log(error);
      localStorage.removeItem('jwt');
      sessionStorage.removeItem('jwt');
      setUser(null)
    })
  };

  // Handle signing up
  const signup = async(user) => {
    if(user.accountType === 'flatee'){
      const userParam = {
        username: user.username,
        password: user.password,
        firstName: user.firstName,
        lastName: user.lastName,
        email: user.email,
        dob: user.dob,
        role: user.accountType.toLowerCase(),
        preferredArea: user.preferredArea,
        checklist: user.checklist,
        rentUnits: "Per Week",
      };
      
      // Call API to register new user
      api.register({userParam})
      .then(function (response) {
        console.log(response)
        return response;
    })
    .catch(function (error) {
        console.log(error)
        return error;
    });
    } else if(user.accountType === 'flat'){
      const userParam = {
        username: user.username,
        password: user.password,
        firstName: user.firstName,
        lastName: user.lastName,
        email: user.email,
        dob: user.dob,
        role: user.accountType.toLowerCase(),
        address: user.address,
        description: user.description,
        existingFlatmates: user.existingFlatmates,
        leaseDate: user.leaseDate,
        flatRules: user.flatRules,
      };

      // Call API to register new user
      api.register({userParam})
      .then(function (response) {
        console.log(response)
        return response;
    })
    .catch(function (error) {
        if(Config.Logging)
            console.log(error)
        return error;
    });
    }

    return signin(user.username, user.password, false);
  };
  return { user, setUser, jwt, isAuthed, signin, signout, signup };
};

export const Role = {
    Admin: 'admin',
    Flat: 'flat',
    Flatee: 'flatee'
};
