import React, { useState } from 'react';
import {Button, Container, CssBaseline, TextField, FormControlLabel, Checkbox, Link, Grid, Box, Typography} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Alert from '@material-ui/lab/Alert';
import { Link as RouterLink, Redirect } from "react-router-dom";
import { Config } from '../../../config'
import { useAuth } from "../../App/Authentication";
import { MemoryRouter as Router } from 'react-router';
import {ReactComponent as FlinderLogo} from '../../../assets/logo.svg';


function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright  '}
      <Link color="inherit" href={`${Config.AppURL}`}>
        {`${Config.AppName}`}
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(3, 0, 2),
  },
}));


const Login = ({ location }) => {
  const classes = useStyles();
  const { signin, isAuthed } = useAuth();

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [remember, setRemember] = useState(false);
  const [error, setError] = useState('');

  // Redirect to previous page or home page
  let { from } = location.state || { from: { pathname: "/" } };

  const handleLogin = (e) => {
    e.preventDefault();
    signin(username, password, remember)
      .then((res) => {
        console.log(res?.message)
        if (res?.error || res?.message) { 
          // Display Error Message
          setError(res?.error || res?.message);
        }
      })
      .catch((error) => {
        console.log(error)
        //   setError(error?.error);
      });
  };

  const handleCheckBoxChange = (event) => {
    setRemember(event.target.checked);
  };

  return (
    <>
    {isAuthed && <Redirect to={from} />}
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <FlinderLogo className = "logo-display"/>

        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            label="Username"
            type="username"
            name="username"
            value={username}
            autoComplete="username"
            autoFocus
            onChange={(e) => setUsername(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            value={password}
            autoComplete="current-password"
            onChange={(e) => setPassword(e.target.value)}
          />
          {error && (
            <Alert severity="error">{error}</Alert>
          )}

          <FormControlLabel
            control={<Checkbox 
              name="rememberme"
              value="remember" 
              color="primary" 
              checked={remember}
              onChange={handleCheckBoxChange}
            />}
            label="Remember me"
          />
          <Button
            id="sign-in"
            type="button"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={(e) => handleLogin(e)}
          >
            Sign In
          </Button>

          <Grid container>
            <Router>
              <Grid item xs>
                <Link href="/forgot" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
                <Link href="/register" variant="body2">
                  Don&apos;t have an account? Sign Up
                </Link>
                {/* <RouterLink to="/register">
                      I don't have an account
                    </RouterLink> */}

              </Grid>
            </Router>
          </Grid>
            
        </form>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
    </>
  );
}

export default Login;